let target = process.env.WEMIX_SDK_TARGET || "production";
const env = environment[target];
console.log("init wemix", target);

// setStorage 메소드를 사용하여 localStorage, sessionStorage 를 선택할 수 있습니다. 두번째 인자는 토큰이 저장될 key이름 입니다.
