Wemix.prototype.goMarket = function () {
  let mobile = /iphone|ipad|ipod|android/i.test(
    navigator.userAgent.toLowerCase()
  );
  let OS = "";
  if (mobile) {
    // 유저에이전트를 불러와서 OS를 구분합니다.
    let userAgent = navigator.userAgent.toLowerCase();

    if (userAgent.search("android") > -1) OS = "android";
    else if (
      userAgent.search("iphone") > -1 ||
      userAgent.search("ipod") > -1 ||
      userAgent.search("ipad") > -1
    )
      OS = "ios";
    else OS = "etc";
  } else {
    // 모바일이 아닐 때
    OS = "nomobile";
  }

  if (OS == "android") {
    location.href =
      "https://play.google.com/store/apps/details?id=com.wemadetree.wemixwallet&hl=ko&gl=US";
  } else if (OS == "ios") {
    window.location = "https://apps.apple.com/kr/app/wemix-wallet/id1531057807";
  }
};
